import React from "react";
import styled from "styled-components";
import { SeoContainer } from "@components/Container/seoContainer";
import { graphql } from "gatsby";

const Layout = styled.div.withConfig({ componentId: "NotFoundPage_Layout" })`
  .main-body {
    display: flex;
    justify-content: center;
    align-items: center;
  }
`;

const NotFoundPage = () => {
  return <Layout>Page not found</Layout>;
};

export default NotFoundPage;

export const Head = () => <SeoContainer title="Oops ... not found" />;

export const query = graphql`
  query ($language: String!) {
    locales: allLocale(filter: { language: { eq: $language } }) {
      edges {
        node {
          ns
          data
          language
        }
      }
    }
  }
`;
